.container.pay {
  display: flex;
  flex-direction: column;
  flex: 1;
  background: #F7F7F7;
}
.container.pay .content {
  flex: 1;
  overflow-y: scroll;
}
.container.pay .content .payInfoContent {
  overflow: hidden;
  margin: 0.5rem 0.5rem 0rem 0.5rem;
  background-color: white;
  border-radius: 0.5rem;
  padding: 0rem 0.75rem 0.45rem 0.75rem;
}
.container.pay .content .payInfoContent .rowBox {
  margin-top: 0.45rem;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
}
.container.pay .content .payInfoContent .rowBox:first-child {
  height: 2.55rem;
  margin-top: 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  border-bottom: 1px solid #F4F4F4;
}
.container.pay .content .payInfoContent .rowBox .title {
  font-size: 0.7rem;
  color: #333333;
  font-weight: bold;
  width: 4.55rem;
}
.container.pay .content .payInfoContent .rowBox .value {
  font-size: 0.7rem;
  color: #333333;
  line-height: 1rem;
  flex: 1;
}
.container.pay .content .payPicContent {
  margin: 0.5rem 0.5rem 0rem 0.5rem;
  background-color: white;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0rem 0.75rem 0rem 0.75rem;
  height: 2.5rem;
}
.container.pay .content .payPicContent .picTitle {
  font-size: 0.75rem;
  color: #333333;
  font-weight: bold;
}
.container.pay .content .payPicContent .picText .currency {
  font-size: 0.6rem;
  color: #E92900;
  font-weight: bold;
}
.container.pay .content .payPicContent .picText .num {
  font-size: 1rem;
  color: #E92900;
  font-weight: bold;
}
